import uniPopup from '@/components/uni-popup/uni-popup.vue'
var util = require('../../../common/util.js');
var formatLocation = util.formatLocation;

export default {
	components: {
		uniPopup
	},
	data() {
		return {
			title: 'getLocation',
			hasLocation: false,
			location: {},
			type: ''
		}
	},
	methods: {
		togglePopup(type) {
			this.type = type;
		},
		showConfirm() {
			this.type = 'showpopup';
		},
		hideConfirm() {
			this.type = '';
		},
		async getLocation() {

			let status = await this.getSetting();
			if (status === 2) {
				this.showConfirm();
				return;
			}


			this.doGetLocation();
		},
		doGetLocation() {
			uni.getLocation({
				success: (res) => {
					this.hasLocation = true;
					this.location = formatLocation(res.longitude, res.latitude);
				},
				fail: (err) => {






					if (err.errMsg.indexOf("auth deny") >= 0) {
						uni.showToast({
							title: "访问位置被拒绝"
						})
					} else {
						uni.showToast({
							title: err.errMsg
						})
					}

				}
			})
		},
		getSetting: function() {
			return new Promise((resolve, reject) => {
				uni.getSetting({
					success: (res) => {
						if (res.authSetting['scope.userLocation'] === undefined) {
							resolve(0);
							return;
						}
						if (res.authSetting['scope.userLocation']) {
							resolve(1);
						} else {
							resolve(2);
						}
					}
				});
			});
		},
		openSetting: function() {
			this.hideConfirm();
			uni.openSetting({
				success: (res) => {
					if (res.authSetting && res.authSetting['scope.userLocation']) {
						this.doGetLocation();
					}
				},
				fail: (err) => {}
			})
		},
		clear: function() {
			this.hasLocation = false
		}
	}
}
